import React, { Component } from 'react'
import './NoteForm.css'

class NoteForm extends Component {
  
  constructor(props) {
    super(props)
    this.state = {
      code : "#include<iostream>\n#include<bits/stdc++.h>\n\nusing namespace std;\n\nint main(int argc, char *argv[]){\n\n\n\n   return 0;\n\n}"
    };
    
  };

  reset = () => {
    document.getElementById('source').value = this.state.code;
  }
  
  inputChange = () => {
    this.props.prgmChange(document.getElementById('source').value)
  }
  
  render(){
    return (
      <div class="NoteForm">
        <div class="form-actions">
          <button type="button" onClick={this.reset}>
            <i class="fa fa-undo"></i>
          </button>
        </div>
        <form>
          <p>
            <input
              type="text"
              name="title"
              placeholder="Title your code"
            />
          </p>
          <textarea 
          required
          name="solution"
          onChange = {this.inputChange}
          id="source"
          value={this.props.prgm}
          >
            {/* {this.state.code} */}
          </textarea>
        </form>
      </div>
          
  )
  };
    
}

export default NoteForm