import React, { Component } from 'react';
import DeviceOrientation, { Orientation } from 'react-screen-orientation'
import Sidebar from './Sidebar'
import NoteForm from './NoteForm'
import NoteListInp from './NoteListInp'
import NoteListOut from './NoteListOut'
import SplitPane from 'react-split-pane';
import './Split.css'
import './App.css';



class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      input: localStorage.getItem('input')||`#include<iostream>\n#include<bits/stdc++.h>\n\nusing namespace std;\n\nint main(int argc, char *argv[]){\n\n\n\n   return 0;\n\n}`,
      output: ``,
      // language_id:localStorage.getItem('language_Id')|| 2,
      language_id: 2,
      user_input: ``,
      outputText: ``
    };
  }
  // submit = () => {
  //   alert('clicked sidebar')
  // }
  input = (abc) => {
 
    // event.preventDefault();
  
    // this.setState({ input: event.target.value });
    this.setState({ input: abc });
    localStorage.setItem('input', abc)
 
  };
  userInput = (xyz) => {
    // event.preventDefault();
    // this.setState({ user_input: event.target.value });
    this.setState({ user_input: xyz });
  };
  // language = (event) => {
   
  //   event.preventDefault();
   
  //   this.setState({ language_id: event.target.value });
  //   localStorage.setItem('language_Id',event.target.value)
   
  // };

  submit = async (e) => {
    e.preventDefault();

  //   let outputText = document.getElementById("output");
  //   outputText.innerHTML = "";

  //   outputText.innerHTML += "Creating Submission ...\n";
        this.setState({outputText : this.state.outputText + "Creating Submission ...\n"});
      const response = await fetch(
        "https://judge0-extra.p.rapidapi.com/submissions",
        {
          method: "POST",
          headers: {
            "x-rapidapi-host": "judge0-extra.p.rapidapi.com",
            "x-rapidapi-key": "6d2755b8ddmshbd7d724ecd2116dp17869ajsn5fc004ca2557", // Get yours for free at https://rapidapi.com/hermanzdosilovic/api/judge0
            "content-type": "application/json",
            accept: "application/json",
          },
          body: JSON.stringify({
            source_code: this.state.input,
            stdin: this.state.user_input,
            language_id: this.state.language_id,
          }),
        }
      );
      this.setState({outputText : this.state.outputText + "Submission Created ...\n"});

      // this.state.outputText += "Submission Created ...\n";
      const jsonResponse = await response.json();

      let jsonGetSolution = {
        status: { description: "Queue" },
        stderr: null,
        compile_output: null,
      };

    while (
      jsonGetSolution.status.description !== "Accepted" &&
      jsonGetSolution.stderr == null &&
      jsonGetSolution.compile_output == null
    ) {
      this.setState({outputText :  `Creating Submission ... \nSubmission Created ...\nChecking Submission Status\nstatus : ${jsonGetSolution.status.description}`});
      // this.state.outputText = `Creating Submission ... \nSubmission Created ...\nChecking Submission Status\nstatus : ${jsonGetSolution.status.description}`;
      if (jsonResponse.token) {
        let url = `https://judge0-extra.p.rapidapi.com/submissions/${jsonResponse.token}?base64_encoded=true`;

        const getSolution = await fetch(url, {
          method: "GET",
          headers: {
            "x-rapidapi-host": "judge0-extra.p.rapidapi.com",
            "x-rapidapi-key": "6d2755b8ddmshbd7d724ecd2116dp17869ajsn5fc004ca2557", // Get yours for free at https://rapidapi.com/hermanzdosilovic/api/judge0
            "content-type": "application/json",
          },
        });

        jsonGetSolution = await getSolution.json();
      }
    }
    if (jsonGetSolution.stdout) {
      const output = atob(jsonGetSolution.stdout);

      // this.state.outputText = "";
      this.setState({outputText: ``});

      // this.state.outputText += `Results :\n${output}\nExecution Time : ${jsonGetSolution.time} Secs\nMemory used : ${jsonGetSolution.memory} bytes`;
      this.setState({outputText: this.state.outputText + `Results :\n${output}\nExecution Time : ${jsonGetSolution.time} Secs\nMemory used : ${jsonGetSolution.memory} bytes`})
    } else if (jsonGetSolution.stderr) {
      const error = atob(jsonGetSolution.stderr);

      // this.state.outputText = "";
      this.setState({outputText: ``});

      // this.state.outputText += `\n Error :${error}`;
      this.setState({outputText: this.state.outputText += `\n Error :${error}`});

    } else {
      const compilation_error = atob(jsonGetSolution.compile_output);

      // this.state.outputText = "";
      this.setState({outputText: ``});

      this.setState({outputText: this.state.outputText += `\n Error :${compilation_error}`});

      // this.state.outputText += `\n Error :${compilation_error}`;
    }
  };


  render(){
    return (
      <DeviceOrientation lockOrientation={'portrait-primary'}>
        <Orientation orientation='portrait-primary'>
          <div className="App">
            <SplitPane split="vertical" allowResize={false}>  
                <SplitPane allowResize={false}>
                  <Sidebar run={this.submit}/>
                </SplitPane>
                <SplitPane split="vertical" minSize={50} maxSize={-60}>
                    <SplitPane split="horizontal" minSize={50} maxSize={-60} defaultSize="50%">
                      <NoteListInp given={this.userInput}/>
                      <NoteListOut out={ this.state.outputText } >
                      </NoteListOut>
                    </SplitPane>
                    
                  <NoteForm prgm={this.state.input} prgmChange={this.input}/>
                </SplitPane>
            </SplitPane>
          </div>
        </Orientation>
      </DeviceOrientation>

      // {/* //  <div className="App">
      // //    <Main/>
      // //  </div> */}

    );
  }
}

export default App;
