import React, { Component } from 'react'
import './NoteList.css'

class NoteListOut extends Component  {
  constructor(props){
    super(props);
    this.state = {
      output : props.out
    }
  }
  
  render(){
    return (
      <div class="NoteList">
      <h3><bold>Project Alif</bold></h3>
      <ul id="notes">
          <li>
            <div class="note">
              <div class="note-title">
                Output
              </div>
              <div class="note-body">
                <form>
                <textarea name="body" id="o/p" value={this.props.out}>
                </textarea>
                </form>
              </div>
            </div>
          </li>
      </ul>
    </div>
    )
  };
    

}

export default NoteListOut